events.on('ready', function() {

    

    $('.btn').on('click', function() {
        $(this).addClass('visited'); // Adiciona a classe 'visited' ao botão clicado
        
        // Pega o value do botão clicado e adiciona "visitado"
        const buttonValue = $(this).val();
        $(this).val(buttonValue + ' - visitado'); // Atualiza o value do botão

        virificarCheck(this); // Passa o botão clicado como argumento

        // Verifica se todos os botões têm a classe 'visited' após o clique
        if ($('.btn').length === $('.btn.visited').length) {
            $('.boxInfo1').addClass('hide');
            $('.boxInfo2').removeClass('hide');
        }
    });

    function virificarCheck(button) {
        if ($(button).hasClass('visited')) {
            $(button).find('p').addClass('hide'); // Esconde o parágrafo dentro do botão
            $(button).find('.ico').removeClass('hide'); // Mostra o ícone dentro do botão
        }
    }

    function verificaTela() {
        var ex1 = scorm.loadObject('pag05');
        var ex2 = scorm.loadObject('pag06');
        var ex3 = scorm.loadObject('pag07');
        var ex4 = scorm.loadObject('pag08');
        var ex5 = scorm.loadObject('pag09');
        var ex6 = scorm.loadObject('pag10');
        var ex7 = scorm.loadObject('pag11');
        var ex8 = scorm.loadObject('pag12');
        var ex9 = scorm.loadObject('pag13');
        if ( ex1 ) {
            $('.btn1').addClass('visited');
        }
        if ( ex2 ) {
            $('.btn2').addClass('visited');
        }
        if ( ex3 ) {
            $('.btn3').addClass('visited');
        }
        if ( ex4 ) {
            $('.btn4').addClass('visited');
        }
        if ( ex5 ) {
            $('.btn5').addClass('visited');
        }
        if ( ex6 ) {
            $('.btn6').addClass('visited');
        }
        if ( ex7 ) {
            $('.btn7').addClass('visited');
        }
        if ( ex8 ) {
            $('.btn8').addClass('visited');
        }
        if ( ex9 ) {
            $('.btn9').addClass('visited');
        }

        // Verifica se algum botão já tem a classe 'visited'
        $('.btn').each(function() {
            if ($(this).hasClass('visited')) {
                $(this).find('p').addClass('hide'); // Esconde o parágrafo dentro do botão
                $(this).find('.ico').removeClass('hide'); // Mostra o ícone dentro do botão

                // Pega o value do botão e adiciona "visitado"
                const buttonValue = $(this).val(); // Obtém o value do botão
                $(this).val(buttonValue + ' - visitado'); // Atualiza o value do botão
            }
        });

        // Verifica se todos os botões têm a classe 'visited'
        if ($('.btn').length === $('.btn.visited').length) {
            $('.boxInfo1').addClass('hide');
            $('.boxInfo2').removeClass('hide');
        }
    }

    setTimeout(() => {
        verificaTela();
    }, 200);
});
